import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { Panel, Cell, Group, Avatar, PanelHeader, List, Spinner, PullToRefresh, HeaderButton, Button, Tooltip, Div, FixedLayout, Tabs, TabsItem } from "@vkontakte/vkui";
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter';

import Icon24Back from "@vkontakte/icons/dist/24/back";
import Icon16Gift from "@vkontakte/icons/dist/16/gift";
import Icon24Discussions from '@vkontakte/icons/dist/24/discussions';
import Icon24Poll from "@vkontakte/icons/dist/24/poll";
import Icon24Info from '@vkontakte/icons/dist/24/info';
import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon24Unpin from '@vkontakte/icons/dist/24/unpin';
import Icon24Settings from '@vkontakte/icons/dist/24/settings';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24Filter from '@vkontakte/icons/dist/24/filter';
import { fireEvent } from "../utils";
import Icon24LikeOutline from '@vkontakte/icons/dist/24/like_outline';
import Icon24ThumbUp from '@vkontakte/icons/dist/24/thumb_up';
import Icon24ThumbDown from '@vkontakte/icons/dist/24/thumb_down';

const Home = ({ id, user, go, transactions, cloud, transactionSelect, resetTransactions, loadUser, topPlace, openProfile, cloudUpdate }) => {
	const [loading, setLoading] = useState(false);
	const [discussionLinkTooltip, setDiscussionLinkTooltip] = useState(false);

	const refresh = async () => {
		setLoading(true);
		resetTransactions();
		await loadUser();
		setLoading(false);
	};

	useEffect(() => {
		setDiscussionLinkTooltip(cloud.loaded ? !cloud.discussion_tooltip : false);
	}, [cloud.discussion_tooltip, cloud.loaded]);

	return (
		<Panel id={id}>
			<PanelHeader
				left={
					<Tooltip
						text="Чтобы открыть беседу, нажмите кнопку. "
						isShown={discussionLinkTooltip}
						onClose={() => {
							setDiscussionLinkTooltip(false);
							cloudUpdate({ param: "discussion_tooltip", value: true});
						}}
						offsetX={5}
						offsetY={5}
					>
						<HeaderButton onClick={() => fireEvent("https://vk.me/join/AJQ1d/NhfhBeG2emVD3fKQsj")}>
						</HeaderButton>
					</Tooltip>
				}
			>
				Схема Метро
			</PanelHeader>
			<PullToRefresh
				onRefresh={refresh}
				isFetching={loading}
			>
				{user &&
				<Group title="">
				</Group>}
				<Group>
             <Div>
        Перед использованием сервиса, пожалуйста, выберите город. 
				<br>
				</br>
				<br>
				</br>
				Если не выбрать город, Вы не сможете пользоваться сервисом.  
            </Div>
              </Group>
				
				<Group title="Россия">
              <List>
                <Cell expandable onClick={() => go("stories")}>Москва</Cell>
                <Cell expandable onClick={() => go("about")}>Санкт-Петербург</Cell>
				<Cell expandable onClick={() => go("transaction")}>Казань</Cell>
				<Cell expandable onClick={() => go("top")}>Новосибирск</Cell>
              </List>
            </Group>
						<Group>
     <Div>
       <Button component="a" href="https://vk.me/schemm">Добавить свой город</Button>
     </Div>
   </Group>
		</PullToRefresh>
		</Panel>
	);
};

const mapProps = (state) => ({
	user: state.user.vk,
	transactions: state.transactions.list,
	topPlace: state.top.findIndex(x => x.id === state.user.vk.id) + 1,
	cloud: state.cloud
});

const mapDispatch = ({ navigator: { goForward }, transactions: { select, reset }, user: { load, openProfile }, cloud: { changeParam } }) => ({
	go: goForward,
	transactionSelect: select,
	resetTransactions: reset,
	loadUser: load,
	openProfile,
	cloudUpdate: changeParam
});

export default connect(mapProps, mapDispatch)(Home);
